
/*GENERAL..................................................................................................................................*/

* {

    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/*.navbar,*/
.navbar__row,
.page__wrapper 
{

    overflow: hidden;
}

#root {

    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 0.95rem;
        background: beige;

        min-height: 100vh;
}

h1{

    margin-top:15px  ;
    color: steelblue;
}

p{

    margin-top:10px  ;
}

hr{

    margin: 15px;
    border: 1px solid steelblue;
}

h3{

    color: steelblue;
}

select{

   border: 2px solid steelblue;
   color: steelblue;
}

button:hover{

    color: yellow;
}

ul{

    list-style-type: none ;
}


/*COMPONENTS...................................................................................................................................*/

/*Posts...........................................................................................................*/
.posts{

    width: 80%;
}

.posts h1{

    font-family:'Time New Roman', serif;

}

.post {

    display: flex;
    flex-direction: column;
    padding: 15px;
    border: 2px solid steelblue;
    margin-top: 15px;
    justify-content: space-between;
    width: 100%;
}

.post__id__page{

        /*height: 100vh;*/

}

.post-btn {

  margin-right: 0.5rem;
}

.post-enter {

  transform: translateX(350px);
}

.post-enter-active {

  transform: translateX(0px);
  transition: all 500ms ease-in;
}

.post-exit {

  opacity: 1;
}

.post-exit-active {

  transform: translateX(-350px);
  transition: all 500ms ease-in;
}


/*Post....................................................................................................*/
.post-item{

    display: flex;
    flex-direction: row;
}
.post__btns{

   display: flex;
   flex-direction: column;
}


.post__btns button{

    margin-bottom: 10px;
}


/*navbar...................................................................................................*/
.navbar {

    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 10px;
    background:steelblue ;
    margin-bottom: 50px;
}

.navbar__row{

    width: 80%;
    display: flex;
    justify-content: space-between;
}

.navbar .logo{

    font-family:'Time New Roman', serif;
    font-size: 3.0rem;
    color: lightblue;
}


.navbar__links {

    display: flex;
    justify-content: flex-end;
    width: 25%;
    align-items: center;
    overflow: visible;

}

.navbar__links div{

    margin-left: 10%;
    overflow: visible;
}


/*link...................................................................................................*/
.link{

    color: white;
    color: lightblue;
    font-size:1.5rem ;
    text-decoration: none;
 }

.link:hover{
    color: yellow;
}

/*page.....................................................................................................*/
.page__wrapper {
    margin-top: 30px;
    display: flex;
}

.page__current {
    border: 2px solid orange;
    font-weight: bold;
}

.page__wrapper{
    margin-bottom: 50px;
    display: flex;
      flex-wrap: wrap;

}

.page {
    border: 2px solid steelblue;
    margin: 5px;
      padding: 10px;
    /*border: 1px solid teal;*/
    cursor: pointer;
    color: steelblue;

}

.page:hover{
    border:2px solid yellow ;
    background:yellow ;
    color: white;
}

.page__current{
    border:2px solid yellow ;
    background:yellow ;
    color: steelblue;
}


/*page-about.....................................................................................................*/
.page__about{
    color: steelblue;
    font-weight: bold;
    width: 80%;
    margin-bottom: 3.3rem;
}

.post__id__page{
    width: 80%;
}


