


@media screen and (max-width:900px) {


    .post-item{

        flex-direction: column;
    }

    .post__btns{

       flex-direction: row;
       justify-content: flex-end;
       magin-left: auto;
       margin-top: 10px;
   }

   #root     {

    /*font-size: 20px;*/
}
}

@media screen and (max-width:768px){

  #root     {

    font-size: 0.9rem;
}
a{

    font-size: 20px !important;
    color: red; 
}

.navbar .logo{

    font-size: 2.5rem;
}

}

@media screen and (max-width:600px){

   #root{

    font-size: 0.85rem;
}

button{

  /*font-size: 0.75rem !important;*/

}

.navbar__links {

  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
}

}

@media screen and (max-width:500px){

   #root{

    font-size: 0.80rem;
}

.navbar__row{

    flex-direction: column;
    justify-content: center;

}

.navbar .logo{

    font-size: 2.25rem;
    text-align: center;
}

.navbar__links {

    margin-left:0 ;
    width:100% ;
}

.navbar__links div{

    margin:5px ; 
}

}