}

}

@media screen and (max-width:600px){

   #root{

    font-size: 0.85rem;
}

button{

  /*font-size: 0.75rem !important;*/

}

.navbar__links {

  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
}

}

@media screen and (max-width:500px){

   #root{

    font-size: 0.80rem;
}

.navbar__row{

    flex-direction: column;
