}

.navbar .logo{

    font-size: 2.25rem;
    text-align: center;
}

.navbar__links {

    margin-left:0 ;
    width:100% ;
}

.navbar__links div{

    margin:5px ; 
}

}
.Loader_loader__1Beiu {

    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 4px dashed steelblue;
    -webkit-animation: Loader_rotate__2WPuS 2s infinite linear;
            animation: Loader_rotate__2WPuS 2s infinite linear;
}

@-webkit-keyframes Loader_rotate__2WPuS {
    
    from {
        -webkit-transform: rotate(0deg) scale(1);
                transform: rotate(0deg) scale(1);
    }
    to {
        -webkit-transform: rotate(360deg) scale(1.4) ;
                transform: rotate(360deg) scale(1.4) ;
    }
}

@keyframes Loader_rotate__2WPuS {
    
    from {
        -webkit-transform: rotate(0deg) scale(1);
                transform: rotate(0deg) scale(1);
    }
    to {
        -webkit-transform: rotate(360deg) scale(1.4) ;
                transform: rotate(360deg) scale(1.4) ;
    }
}
